:root {
  --color-primero:#7d3c98;
  --color-primeroDark:#8e44ad;
  --color-segundo:#008080;
  --color-tercero:#ff0;
  --color-cuarto: #750D37;
  --blanco: #fffffe;
  --negro: #151516;
  --color-nota: #b8bfc6;
  --bg-nota: #363b40;
  --scrollOne:#48D1CC;
  --scrollTwo:#4682B4;
  --fuenteHeading: "Original Surfer", cursive;
  --fuenteText: "Handlee", cursive;
}

.dark {
  background-color: var(--negro);
}
.dark h1,
.dark h2,
.dark h3,
.dark h4,
.dark p,
.dark li,
.dark a {
  color: var(--blanco);
}
.dark .icons {
  filter: invert(100%);
}
.dark .barra {
  background-color: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(20px);
  border-right: 0.1rem solid rgba(255, 255, 255, 0.5);
}
.dark .nav__link:hover {
  background-color: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(20px);
  border: 0.1rem solid rgba(255, 255, 255, 0.5);
}
.dark .banner {
  background-image: url("../assets/media/imagen/sky_night.jpg");
}
.dark .box {
  background: linear-gradient(145deg, #222222, #292929);
  box-shadow: 20px 20px 60px #202020, -20px -20px 60px #2c2c2c;
}
.dark .footer {
  background-color: var(--blanco);
  color: var(--negro);
}
.dark .footer h3 {
  color: var(--negro);
}
.dark .carta::after {
  background-color: var(--blanco);
}
.dark .galeria__img {
  outline: 1px solid var(--blanco);
}
.dark .galeria__contenido {
  background-color: rgba(255, 255, 255, 0.2);
}
.dark .galeria__icon {
  filter: invert(0);
}
.dark .galeria__copy {
  color: var(--negro);
  background-color: rgba(255, 255, 255, 0.6);
}
.dark .link {
  color: var(--color-cuarto);
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-behavior: smooth;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.5;
  font-family: var(--fuenteText);
  transition: 0.5s;
}

img {
  max-width: 100%;
  display: block;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

h1,
h2,
p,
ul {
  padding: 0;
  margin: 0;
}

.wrapper {
  max-width: 116rem;
  width: 60%;
  margin: 0 auto;
}
@media screen and (min-width: 768px) {
  .wrapper {
    width: 80%;
  }
}

.center {
  text-align: center;
}

.writings__title {
  margin: 4rem 0 0 0;
  font-size: 3rem;
}
@media screen and (min-width: 768px) {
  .writings__title {
    font-size: 3.8rem;
  }
}
.writings__subtitle {
  font-size: 2rem;
}
.writings__text {
  margin: 1.6rem 0;
}

.title {
  margin: 3rem 0;
  font-size: 2rem;
}
@media screen and (min-width: 768px) {
  .title {
    font-size: 3.8rem;
  }
}

.goup {
  display: none;
}
@media screen and (min-width: 1024px) {
  .goup {
    display: flex;
    position: fixed;
    bottom: 0.2rem;
    right: 0.2rem;
    width: 4rem;
    height: 4rem;
  }
}

.goup__icon {
  width: 0rem;
  height: 0rem;
  border-radius: 50%;
  background: #008080;
  box-shadow: inset -5px -5px 10px #003333, inset 5px 5px 10px #00cdcd;
  -webkit-box-shadow: -2px -3px 17px 3px #551A8B;
  box-shadow: -2px -3px 17px 3px #551A8B;
  transition: 0.2s;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.goup__icon:hover {
  cursor: pointer;
}

.show {
  z-index: 10;
}
.show .goup__icon {
  animation: popup 0.3s ease-in-out;
  width: 4rem;
  height: 4rem;
  z-index: 10;
}
@keyframes popup {
  0% {
    width: 0rem;
    height: 0rem;
  }
  50% {
    width: 6rem;
    height: 6rem;
  }
  100% {
    width: 4rem;
    height: 4rem;
  }
}

/* =====BARRA LATERAL MENU===== */
.barra {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 5rem;
  height: 100vh;
  color: var(--negro);
  padding: 0.5rem;
  transition: 0.5s;
  z-index: 100;
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border-right: 0.1rem solid rgba(0, 0, 0, 0.5);
}
@media screen and (min-width: 1024px) {
  .barra {
    width: 6rem;
  }
}

.barra__nav {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow: hidden;
}

.barra__nav-brand {
  visibility: hidden;
}
@media screen and (min-width: 1024px) {
  .barra__nav-brand {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-bottom: 2rem;
    visibility: visible;
  }
}

.nav__toggle {
  width: 5rem;
  padding: 0.5rem;
  cursor: pointer;
}

.nav__logo {
  color: var(--negro);
  font-weight: 600;
  font-size: 1.2rem;
}

.nav__link {
  display: grid;
  grid-template-columns: max-content max-content;
  align-items: center;
  column-gap: 2rem;
  padding: 1rem;
  color: var(--negro);
  border-radius: 0.5rem;
  margin-bottom: 2rem;
  transition: 0.3s;
  cursor: pointer;
  position: relative;
  border: 0.1rem solid transparent;
}
.nav__link:hover {
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border: 0.1rem solid rgba(0, 0, 0, 0.5);
}

.nav__icon {
  width: 2rem;
}
@media screen and (min-width: 1024px) {
  .nav__icon {
    width: 3rem;
  }
}

.nav__name {
  font-size: 1.5rem;
}

.expander {
  width: 17rem;
}

/* =====HERO===== */
.hero {
  display: grid;
}
@media screen and (min-width: 768px) {
  .hero {
    grid-template-columns: 2fr 1fr;
  }
}

@media screen and (min-width: 1024px) {
  .hero__images img {
    height: 66rem;
    width: 100%;
    object-fit: cover;
  }
}
.hero__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 3rem;
  font-family: var(--fuenteHeading);
}
@media screen and (min-width: 768px) {
  .hero__text {
    padding-top: 0;
    row-gap: 2rem;
  }
}

@media screen and (min-width: 768px) {
  .hero__title {
    font-size: 4rem;
  }
}
@media screen and (min-width: 1024px) {
  .hero__title {
    font-size: 6rem;
  }
}

@media screen and (min-width: 768px) {
  .hero__subtitle {
    font-size: 2.2rem;
  }
}

.name {
  color: var(--color-cuarto);
}
@media screen and (min-width: 1024px) {
  .name {
    font-size: 2.5rem;
  }
}

/* =====BANNER===== */
.banner {
  width: 100%;
  text-align: center;
  background-image: url("../assets/media/imagen/sky.jpg");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 5rem 0;
  margin-bottom: 2rem;
  z-index: -1;
  overflow: hidden;
}

.banner__text {
  font-size: 4rem;
  margin: 0;
  position: relative;
  display: inline-block;
  animation: moveOne 15s infinite linear;
}
@media screen and (min-width: 376px) {
  .banner__text {
    animation: moveTwo 15s infinite linear;
  }
}
@media screen and (min-width: 768px) {
  .banner__text {
    animation: moveThree 15s infinite linear;
  }
}
@media screen and (min-width: 1024px) {
  .banner__text {
    animation: moveFour 15s infinite linear;
  }
}

@keyframes moveOne {
  from {
    left: 376px;
  }
  to {
    left: -4800px;
  }
}
@keyframes moveTwo {
  from {
    left: 768px;
  }
  to {
    left: -4800px;
  }
}
@keyframes moveThree {
  from {
    left: 1024px;
  }
  to {
    left: -4800px;
  }
}
@keyframes moveFour {
  from {
    left: 1280px;
  }
  to {
    left: -4800px;
  }
}
/* =====PAGINAS===== */
.box {
  padding: 1rem;
  height: 74rem;
  margin-bottom: 3rem;
  border-radius: 5rem;
  background: linear-gradient(145deg, #e3e3e3, #ffffff);
  box-shadow: 20px 20px 60px #d6d6d6, -20px -20px 60px #ffffff;
}
@media screen and (min-width: 768px) {
  .box {
    height: 62rem;
  }
}
@media screen and (min-width: 1024px) {
  .box {
    height: 50rem;
  }
}

.li__icon {
  opacity: 0;
  color: var(--color-primeroDark);
  width: 2rem;
}

.box__buttom ul {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  row-gap: 2rem;
  padding: 1rem;
}
@media screen and (min-width: 768px) {
  .box__buttom ul {
    flex-direction: row;
    justify-content: space-around;
  }
}
.box__buttom .li {
  cursor: pointer;
  font-size: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 0.5rem;
}
.box__buttom .li.activo {
  color: var(--color-primeroDark);
}
.box__buttom .li.activo .li__icon {
  opacity: 1;
}
.box__buttom .li:hover {
  color: var(--color-primeroDark);
  transition: color 0.5s ease;
}
.box__buttom .li .li a:visited {
  color: var(--color-primeroDark);
}

.box__content {
  position: relative;
}

.box__context {
  display: grid;
  grid-template-rows: min-content 1fr;
  padding: 2rem 1rem 0;
  position: absolute;
  opacity: 0;
  gap: 3rem;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .box__context {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: unset;
    align-items: center;
    justify-content: center;
  }
}
.box__context.activo {
  opacity: 1;
}

.box__img img {
  border-radius: 1rem;
}

.box__text p {
  padding-bottom: 1.5rem;
}
.box__text p:nth-child(1) {
  font-size: 3rem;
  font-family: var(--fuenteHeading);
}
@media screen and (min-width: 768px) {
  .box__text p:nth-child(1) {
    padding-bottom: 3rem;
  }
}

/* =====VIDEOS YOUTUBE===== */
.videos {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));
  gap: 1rem;
  justify-items: center;
  justify-self: center;
  padding-left: 10%;
  padding-right: 10%;
}
@media screen and (min-width: 768px) {
  .videos {
    grid-template-columns: repeat(auto-fill, minmax(30rem, 1fr));
    padding: 0;
  }
}

iframe {
  border: 1px solid var(--color-cuarto);
}

/* =====FOOTER===== */
.footer {
  margin-top: 5rem;
  background-color: var(--negro);
  color: var(--blanco);
  width: 100%;
  padding-bottom: 3rem;
}

.watch {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media screen and (min-width: 1024px) {
  .watch {
    flex-direction: row;
    justify-content: space-around;
  }
}

.watches__content {
  display: flex;
}

.simply-section {
  width: 5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
@media screen and (min-width: 376px) {
  .simply-section {
    width: 10rem;
    margin: 0.5rem;
  }
}

.simply-amount {
  display: block;
  font-size: 2rem;
  font-weight: bold;
}
@media screen and (min-width: 376px) {
  .simply-amount {
    font-size: 2.5rem;
  }
}

.simply-word {
  font-size: 1.2rem;
}
@media screen and (min-width: 376px) {
  .simply-word {
    font-size: 2rem;
  }
}

/* =====LETTERS===== */
.carta {
  margin-bottom: 5rem;
}
.carta::after {
  content: "";
  display: block;
  width: 50%;
  height: 0.1rem;
  background-color: var(--negro);
  transform: translate(50%, -50%);
}

.enlaces {
  width: 15%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0.3rem;
  margin-bottom: 2rem;
}
.enlaces .enlaces__icon {
  color: var(--blanco);
  width: 2.8rem;
}

/* =====GALLERY===== */
.galeria {
  columns: 5 250px;
  column-gap: 0.5em;
  margin-top: 3rem;
}

.galeria__img {
  margin-bottom: 0.5em;
  outline: 1px solid var(--negro);
  outline-offset: -10px;
}
.galeria__img:hover {
  filter: grayscale(50%);
  cursor: pointer;
  transition: filter 0.5s;
}

.galeria__contenido {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  transform: translateX(-100%);
  background-color: rgba(21, 21, 22, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform 0.4s ease-in;
}

.mover {
  transform: translateX(0);
}

.galeria__show {
  height: 50%;
  transform: scale(0);
  display: block;
  object-fit: cover;
  transition: transform 0.5s 0.4s;
  z-index: -1;
}
@media screen and (min-width: 768px) {
  .galeria__show {
    height: 75%;
  }
}

.show {
  transform: scale(1);
}

.galeria__icon {
  position: absolute;
  color: var(--blanco);
  top: 1.4rem;
  right: 1.4rem;
  width: 3rem;
  filter: invert(100%);
  cursor: pointer;
}
@media screen and (min-width: 768px) {
  .galeria__icon {
    width: 3.4rem;
    top: 3rem;
    right: 3rem;
  }
}

.galeria__copy {
  position: fixed;
  color: var(--blanco);
  bottom: 14rem;
  font-size: 1.8rem;
  font-family: var(--fuenteHeading);
  background-color: rgba(21, 21, 22, 0.8);
  border-radius: 1rem;
  padding: 0.5rem 1rem;
}
@media screen and (min-width: 768px) {
  .galeria__copy {
    font-size: 2.6rem;
    bottom: 8rem;
  }
}

/* =====ANECDOTAL===== */
.anecdotario {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 5rem;
}
@media screen and (min-width: 768px) {
  .anecdotario {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    text-align: center;
  }
}

.anecdotario__date {
  flex-grow: 2;
}

.bloque h3 {
  cursor: pointer;
}
.bloque p {
  height: 0;
  padding: 0;
  overflow: hidden;
  transition: all 0.5s ease;
}
.bloque a {
  display: inline-block;
  font-weight: 700;
  padding: 0.5rem;
  margin: 0.3rem;
  border-radius: 1rem;
  color: var(--color-cuarto);
  transition: background-color 0.5s ease, color5s ease;
}
.bloque a:hover {
  background-color: var(--color-segundo);
  color: var(--blanco);
  padding: 0.8rem 1.2rem;
}
.bloque.activo p {
  height: 5rem;
}

.link {
  color: var(--color-cuarto);
  padding: 0.5rem;
  margin: 0.3rem;
  border-radius: 1rem;
  transition: background-color 0.5s ease, color 0.5s ease;
}
.link:hover {
  background-color: var(--color-segundo);
  color: var(--blanco);
  padding: 0.8rem 1.2rem;
}